import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)


const routes = [
    /* 默认跳转到登录页面 */
    {
        path: '/',
        redirect: '/login'
    },
    /* 管理员端页面路由设置 */
    {
        path: '/admin',
        name: 'admin',
        component: () => import('../views/admin.vue'),
        children: [
            {
                path: '/admin/users/1',
                component: () => import('../views/users/index.vue')
            },
            {
                path: '/admin/ansys/1',
                component: ()=>import('../views/ansys/index.vue')
            },
            {
                path: '/admin/ansys/2',
                component: ()=>import('../views/ansys/index2.vue')
            },
            {
                path: '/admin/model',
                component: ()=>import('../views/model/index.vue')
            }
        ]
    },
    {
        path: '/login',
        name: 'login',
        component: () => import('../views/login.vue')
    }
]

const router = new VueRouter({
    routes
})

export default router


